import { Close, KeyboardArrowDown, KeyboardArrowUp } from "@rp/react/web/mui/icons"

type RpJsonToolBarAdornmentProps = {
  foundCount: number
  onArrowsClick: (value: number) => void
  onClose: () => void
  textRef: React.RefObject<HTMLInputElement | null>
  arrowIndex: number
  hasText?: boolean
}

export const RpJsonToolBarAdornment = ({
  foundCount,
  onArrowsClick,
  onClose,
  textRef,
  arrowIndex,
  hasText,
}: RpJsonToolBarAdornmentProps) => {
  return (
    <>
      {hasText ? (
        <>
          <span style={{ fontSize: 12 }}>
            {foundCount ? `${arrowIndex}/${foundCount.toLocaleString()}` : "0/0"}
          </span>
          {Boolean(foundCount) && (
            <KeyboardArrowUp
              data-testid="arrowPrevious"
              sx={{ cursor: "pointer" }}
              onClick={() => {
                onArrowsClick(-1)
              }}
            />
          )}
          {Boolean(foundCount) && (
            <KeyboardArrowDown
              data-testid="arrowNext"
              sx={{ cursor: "pointer" }}
              onClick={() => {
                onArrowsClick(1)
              }}
            />
          )}
        </>
      ) : null}

      <Close
        sx={{ cursor: "pointer" }}
        role="button"
        aria-label="Close find in document"
        aria-hidden="false"
        onClick={() => {
          onClose()
          if (textRef.current) {
            textRef.current.focus()
          }
        }}
      />
    </>
  )
}
